import { utils } from './utils';
import { isBorderBoxFn, getPBMWidth, getWHIgnoreDisplay, MARGIN_INDEX, BORDER_INDEX, CONTENT_INDEX } from './size-utils';

type DomUtilType = {
  getParent: (element: Document | HTMLElement) => Document | HTMLElement | null;
  viewportWidth: (win: Window) => any;
  viewportHeight: (win: Window) => any;
  docWidth: (refWin: Window) => number;
  docHeight: (refWin: Window) => number;
  outerWidth: (el: HTMLElement, includeMargin?: boolean) => number;
  outerHeight: (el: HTMLElement, includeMargin?: boolean) => number;
  width: (elem: HTMLElement, v?: number) => any;
  height: (elem: HTMLElement, v?: number) => any;
};

const domUtils: DomUtilType = {
  getParent(element: Document | HTMLElement): Document | HTMLElement | null {
    let parent: ShadowRoot | Document | DocumentFragment | HTMLElement | ParentNode | null = element;
    do {
      if (parent.nodeType === 11 && (parent as ShadowRoot).host) { // ShadowRoot
        parent = (parent as ShadowRoot).host;
      } else {
        parent = parent.parentNode;
      }
    } while (parent && parent.nodeType !== 1 && !utils.isDocument(parent)); // 非Element节点、document节点
    return parent as HTMLElement | Document;
  },
  viewportWidth(win: Window) {
    // pc browser includes scrollbar in window.innerWidth
    const prop = `clientWidth`;
    const doc = win.document;
    const body = doc.body;
    const documentElement = doc.documentElement;
    const documentElementProp = documentElement[prop as keyof HTMLElement];
    // 标准模式取 documentElement
    // backcompat 取 body
    return (
      (doc.compatMode === 'CSS1Compat' && documentElementProp) ||
      (body && body[prop as keyof HTMLElement]) ||
      documentElementProp
    );
  },
  viewportHeight(win: Window) {
    // pc browser includes scrollbar in window.innerWidth
    const prop = `clientHeight`;
    const doc = win.document;
    const body = doc.body;
    const documentElement = doc.documentElement;
    const documentElementProp = documentElement[prop as keyof HTMLElement];
    // 标准模式取 documentElement
    // backcompat 取 body
    return (
      (doc.compatMode === 'CSS1Compat' && documentElementProp) ||
      (body && body[prop as keyof HTMLElement]) ||
      documentElementProp
    );
  },
  docWidth(refWin: Window): number {
    const d = refWin.document;
    return Math.max(d.documentElement.scrollWidth, d.body.scrollWidth, domUtils.viewportWidth(refWin));
  },
  docHeight(refWin: Window): number {
    const d = refWin.document;
    return Math.max(
      // firefox chrome documentElement.scrollHeight< body.scrollHeight
      // ie standard mode : documentElement.scrollHeight> body.scrollHeight
      d.documentElement.scrollHeight,
      // quirks : documentElement.scrollHeight 最大等于可视窗口多一点？
      d.body.scrollHeight,
      domUtils.viewportHeight(refWin),
    );
  },
  outerWidth(el: HTMLElement, includeMargin?: boolean): number {
    return (
      el &&
      getWHIgnoreDisplay(el, 'width', includeMargin ? MARGIN_INDEX : BORDER_INDEX)
    );
  },
  outerHeight(el: HTMLElement, includeMargin?: boolean): number {
    return (
      el &&
      getWHIgnoreDisplay(el, 'height', includeMargin ? MARGIN_INDEX : BORDER_INDEX)
    );
  },
  width(elem: HTMLElement, v?: number) {
    let val = v;
    if (val !== undefined) {
      if (elem) {
        const isBorderBox = isBorderBoxFn(elem);
        if (isBorderBox) {
          val += getPBMWidth(elem, ['padding', 'border'], ['Left', 'Right']);
        }
        return utils.css(elem, 'width', val);
      }
      return undefined;
    }
    return elem && getWHIgnoreDisplay(elem, 'width', CONTENT_INDEX);
  },
  height(elem: HTMLElement, v?: number) {
    let val = v;
    if (val !== undefined) {
      if (elem) {
        const isBorderBox = isBorderBoxFn(elem);
        if (isBorderBox) {
          val += getPBMWidth(elem, ['padding', 'border'], ['Top', 'Bottom']);
        }
        return utils.css(elem, 'height', val);
      }
      return undefined;
    }
    return elem && getWHIgnoreDisplay(elem, 'height', CONTENT_INDEX);
  },
};

export { domUtils };